@use"../utils/mixins";
@use"../utils/variables.scss";
.button {
    // --width: 100px;
    // --height: 90px;
    // --tooltip-height: 35px;
    // --tooltip-width: 90px;
    // --gap-between-tooltip-to-button: 18px;
    // --button-color: #244548;
    // --tooltip-color: #fff;
    width: 10rem;
    height: 35px;
    background: variables.$color-primary;
    position: relative;
    text-align: center;
    border-radius: 0.45em;
    font-family: "Arial";
    transition: background 0.3s;
    animation: scale 0.8s 0.5s linear infinite;
    transition: all 0.5s ease-in-out;
  }
  @keyframes scale {
    0% {
      box-shadow: 0 0 0px 0px variables.$color-primary;
    }
  
    25% {
      box-shadow: 0 0 15px 2.5px variables.$color-primary;
    }
  
    50% {
      box-shadow: 0 0 30px 5px variables.$color-primary;
    }
  
    75% {
      box-shadow: 0 0 15px 2.5px variables.$color-primary;
    }
  
    100% {
      box-shadow: 0 0 0px 0px variables.$color-primary;
    }
  }
  
